import { h } from 'preact';
import { useEffect, useState, useContext } from 'preact/hooks';
import { route } from 'preact-router';
import { Link } from 'preact-router/match';

import { AuthContext } from "../../components/Auth";
import Welcome_Spinner from '../../components/Welcome_Spinner';
import style from './style.css';

const Buy = () => {
  const { currentUser } = useContext(AuthContext);
  const [activeRoute, setActiveRoute] = useState('/index.html');
  const [localLoading, setLocalLoading] = useState(true);

  useEffect(() => {
    const handleRoute = async () => {
      // Redirect to home on success.

      let queryOptions = { active: true, currentWindow: true };
      let [tab] = await chrome.tabs.query(queryOptions);

      const regex = /admin\/menus\/[0-9]+(\?.*)?$/;
      const regex1 = /store\/.*\/menus\/[0-9]+(\?.*)?$/;
      const regex2 = /admin\/menus\/?(\?.*)?$/;
      const regex3 = /store\/.*\/menus\/?(\?.*)?$/;
      //console.log(tab);

      if (regex.test(tab.url) || regex1.test(tab.url)) {
        setActiveRoute('/menu');
      } else if (regex2.test(tab.url) || regex3.test(tab.url)) {
        setActiveRoute('/menus');
      } else {
        //console.log('%cIn Else Block:', 'color:blue;background:red;padding:5px;');
      }
    };

    handleRoute();
  });

  useEffect(() => {
    //console.log('%cThis is currentUser:', 'color:#FFF;background:#000;padding:5px;', currentUser);
    if (currentUser) {
      //console.log(`[Redirecting] redirect to ${activeRoute}...`);
      route(activeRoute);
    }

    setLocalLoading(false);

  }, [currentUser, activeRoute]);

	return (
    localLoading ? <Welcome_Spinner /> :
      <div class={style.body}>
        <h2>Shopify Navigation Tool</h2>
        <h5>An absolute time saver</h5>

        <h4 class={style.bottom}>One time purchase</h4>
        <div class={style.actions}>
          <a href="https://meraki-apps.com/buy" target="_blank" rel="noreferrer">
            <svg
              height='16px'
              width='16px'
              fill="#000000"
              xmlns="http://www.w3.org/2000/svg"
              version="1.1"
              x="0px"
              y="0px"
              viewBox="0 0 100 100"
              enable-background="new 0 0 100 100"
            >
              <title>Buy</title>
              <g>
                <path d="M26.5,32.8v27.7h3V32.8c0-6.1-1.6-10.1-4.9-12.1c-5-3-11.8,0.4-12.1,0.6l1.4,2.6c0.1,0,5.6-2.8,9.2-0.7    C25.4,24.6,26.5,27.9,26.5,32.8z M86.4,24c-2.7-3.7-9-3.5-9.2-3.5H33.5v3l43.7,0c1.4,0,5.2,0.3,6.7,2.3c0.8,1.1,0.8,2.7,0,4.7    l-11.7,27H33.5v3h40.6l12.6-28.9C87.9,28.6,87.8,26,86.4,24z M62.5,64.5c-4.6,0-8.4,3.5-8.9,8H39.4c-0.5-4.5-4.3-8-8.9-8    c-5,0-9,4-9,9c0,5,4,9,9,9c4.3,0,7.9-3,8.8-7h14.5c0.9,4,4.5,7,8.8,7c5,0,9-4,9-9C71.5,68.5,67.5,64.5,62.5,64.5z M30.5,79.5    c-3.3,0-6-2.7-6-6s2.7-6,6-6c3.3,0,6,2.7,6,6S33.8,79.5,30.5,79.5z M62.5,79.5c-3.3,0-6-2.7-6-6s2.7-6,6-6s6,2.7,6,6    S65.8,79.5,62.5,79.5z M53.5,29.5v9h-9v3h9v9h3v-9h9v-3h-9v-9H53.5z" />
              </g>
            </svg>
            <span class={style.label}>Buy $10</span>
            <span class={style.ball_pulse}>
              <span />
              <span />
              <span />
            </span>
          </a>
        </div>
        <p class={style.footer}>
          Already did? <Link class={style.route} href="/login">Log in</Link> or <Link class={style.route} href="/validate">Validate your license key</Link>
        </p>
      </div>
	);
}

export default Buy;
